.row{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

@for $i from 1 through 12{
    .col-#{$i}{
        width: 100%/12 * $i;
        @include responsive(mobile){
            width: 100%;
        }
        @include responsive(tablet){
            width: 100%;
        }
    }
}