@mixin  responsive($breakpoint) {
    @if $breakpoint =='mobile'{
        @media (max-width: 600px){
            @content;
        }
    }
    @if $breakpoint =='tablet'{
        @media (max-width:900px) {
            @content;
        }
        }
    
    @if $breakpoint =='laptop'{
        @media (max-width: 1200px){
            @content;
        }
    }
     @if $breakpoint =='desktop'{
        @media (min-width: 1200px){
            @content;
        }
     }
}